import {
  Component,
  inject,
  signal,
  computed,
  TemplateRef,
  ViewChild,
  OnInit,
  DestroyRef,
} from '@angular/core';
import { Validators } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { toSignal, toObservable } from '@angular/core/rxjs-interop';
import { catchError, of, tap, switchMap } from 'rxjs';

import { AppStoreService } from '../../services/app-store.service';
import { AppDefinition } from '../../models/app-store.models';
import { ClientDataService } from '../../../clients/services/client-data.service';
import { ModalFormService } from '../../../../shared/services/forms/modal-form.service';
import { ModalListFormComponent } from '../../../../shared/components/forms/modal-list-form/modal-list-form.component';
import {
  createTextAreaField,
  createDropdownField,
} from '../../../../shared/models/form-field.model';
import { AppSubmissionReviewRequest } from '../../models/api/app-store-api.models';

import { ButtonModule } from 'primeng/button';
import { CardModule } from 'primeng/card';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { TooltipModule } from 'primeng/tooltip';
import { GridComponent } from '../../../../shared/components/grid/grid.component';
import { GridColumn } from '../../../../shared/models/grid.model';
import { AppStatusBadgeComponent } from '../../components/app-status-badge/app-status-badge.component';

@Component({
  selector: 'app-admin-panel',
  standalone: true,
  imports: [
    CommonModule,
    RouterModule,
    ButtonModule,
    CardModule,
    ProgressSpinnerModule,
    TooltipModule,
    GridComponent,
    AppStatusBadgeComponent,
  ],
  templateUrl: './app-admin-panel.component.html',
  styleUrls: ['./app-admin-panel.component.scss'],
})
export class AppAdminPanelComponent implements OnInit {
  private readonly appStoreService = inject(AppStoreService);
  private readonly clientDataService = inject(ClientDataService);
  private readonly modalFormService = inject(ModalFormService);
  private readonly destroyRef = inject(DestroyRef);

  @ViewChild('appIconTemplate', { static: true })
  appIconTemplate!: TemplateRef<{ $implicit: AppDefinition }>;
  @ViewChild('statusTemplate', { static: true }) statusTemplate!: TemplateRef<{
    $implicit: AppDefinition;
  }>;
  @ViewChild('actionsTemplate', { static: true })
  actionsTemplate!: TemplateRef<{ $implicit: AppDefinition }>;

  // State Signals
  readonly statusFilter = signal<AppDefinition['status'] | 'ALL'>(
    'pendingReview',
  );
  readonly loading = signal(true);
  readonly error = signal<string | null>(null);
  private readonly refreshTrigger = signal(0);

  readonly columns = signal<GridColumn<AppDefinition>[]>([]);

  // Trigger data fetch when statusFilter or refreshTrigger changes
  private readonly apps$ = toObservable(
    computed(() => ({
      status: this.statusFilter(),
      refresh: this.refreshTrigger(),
    })),
  ).pipe(
    tap(() => {
      this.loading.set(true);
      this.error.set(null);
    }),
    switchMap(({ status }) => {
      const owner = this.clientDataService.getSelectedClient()?.id ?? '';
      const params = status === 'ALL' ? { owner } : { owner, status };
      return this.appStoreService.getApps(params).pipe(
        tap(() => {
          this.loading.set(false);
        }),
        catchError((err) => {
          console.error('Error loading apps:', err);
          this.loading.set(false);
          this.error.set('Unable to load apps. Please try again.');
          return of([] as AppDefinition[]);
        }),
      );
    }),
  );

  readonly apps = toSignal(this.apps$, { initialValue: [] as AppDefinition[] });

  readonly statusOptions: {
    label: string;
    value: AppDefinition['status'] | 'ALL';
  }[] = [
    { label: 'Draft', value: 'draft' },
    { label: 'Pending Review', value: 'pendingReview' },
    { label: 'Under Review', value: 'underReview' },
    { label: 'Changes Requested', value: 'changesRequested' },
    { label: 'Approved', value: 'approved' },
    { label: 'Rejected', value: 'rejected' },
    { label: 'Suspended', value: 'suspended' },
    { label: 'Withdrawn', value: 'withdrawn' },
    { label: 'All', value: 'ALL' },
  ];

  ngOnInit(): void {
    this.setupColumns();
  }

  private setupColumns(): void {
    this.columns.set([
      {
        field: 'name',
        header: 'App',
        sortable: true,
        cellTemplate: this.appIconTemplate,
      },
      {
        field: 'developerName',
        header: 'Developer',
        sortable: true,
      },
      {
        field: 'status',
        header: 'Status',
        sortable: true,
        cellTemplate: this.statusTemplate,
      },
      {
        field: 'createdAt',
        header: 'Submitted',
        sortable: true,
        valueGetter: (app) => new Date(app.createdDate).toLocaleDateString(),
      },
      {
        field: 'actions',
        header: 'Actions',
        cellTemplate: this.actionsTemplate,
      },
    ]);
  }

  onStatusFilterChange(status: AppDefinition['status'] | 'ALL'): void {
    this.statusFilter.set(status);
  }

  openReviewModal(app: AppDefinition): void {
    const dummyField = createTextAreaField('dummy', 'dummy', undefined);

    this.modalFormService
      .openListItemModal<Record<string, string>>({
        modalConfig: {
          modalComponent: ModalListFormComponent,
          modalTitle: 'Review App Submission',
          modalFields: [
            createDropdownField(
              'status',
              'Decision',
              [
                { label: 'Approve', value: 'approved' },
                { label: 'Reject', value: 'rejected' },
                { label: 'Suspend', value: 'suspended' },
                { label: 'Request Changes', value: 'changesRequested' },
              ],
              undefined,
              {
                validations: [
                  { name: 'required', validator: Validators.required },
                ],
              },
            ),
            createTextAreaField(
              'comments',
              'Comments to Developer',
              undefined,
              {
                placeholder: 'Explain the reason for this decision...',
                validations: [
                  { name: 'required', validator: Validators.required },
                ],
              },
            ),
            createTextAreaField('internalNotes', 'Internal Notes', undefined, {
              placeholder: 'Internal notes for the team...',
            }),
          ],
          onSubmit: (data, ref) => {
            const request: AppSubmissionReviewRequest = {
              id: app.id,
              developerId: app.developerId,
              status: data['status'] as AppSubmissionReviewRequest['status'],
              comments: data['comments'],
              internalNotes: data['internalNotes'],
            };

            return new Promise((resolve) => {
              this.appStoreService.reviewApp(app.id, request).subscribe({
                next: () => {
                  this.refreshApps();
                  ref.close();
                  resolve(true);
                },
                error: (err) => {
                  console.error(err);
                  // Ideally show a toast or error message here
                  resolve(false);
                },
              });
            });
          },
        },
        itemData: {
          status: app.status,
          comments: '',
          internalNotes: '',
        },
        destroyRef: this.destroyRef,
        field: dummyField,
      })
      .subscribe();
  }

  private refreshApps(): void {
    this.refreshTrigger.update((n) => n + 1);
  }
}
